import { Component, OnInit } from '@angular/core';
import { ChatSetting } from '../../_models/ChatSetting';
import { Role } from '../../_models/Role';
import { AlertifyService } from '../../_services/alertify.service';
import { LoadspecsService } from '../../_services/loadspecs.service';

@Component({
  selector: 'app-itad-chat-setting',
  templateUrl: './itad-chat-setting.component.html',
  styleUrls: ['./itad-chat-setting.component.css']
})
export class ItadChatSettingComponent implements OnInit {
  model: any = {};
  setrole: string;
  users: ChatSetting[];
  roles: Role[];

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.model.roleID = '0';
  }
  loadMenu(id) {
    return this.loadspecsService.getChatSetting().subscribe((users: ChatSetting[]) => {
      this.users = users.filter(proj => (proj.chat_type === Number(id)));
    });
  }
  activateChat(event, user_id) {
    this.setrole  = this.model.roleID;
    if (event.target.checked === true) {
      this.model = {user_id : user_id, chat_type : this.setrole, status: 1};
    } else if (event.target.checked === false) {
      this.model = {user_id : user_id, chat_type : this.setrole, status: 0};
    }
    this.loadspecsService.activateChat(this.model).subscribe(() => {
        this.model.roleID = this.setrole;
        this.loadMenu(this.setrole);
    }, error => {
      this.alertify.error(error);
    });
  }

}
